body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #f7b42c;
	background-image: -webkit-linear-gradient(135deg, #f7b42c 0%, #fc575e 74%);
	background-image: linear-gradient(315deg, #f7b42c 0%, #fc575e 74%);
	overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
	width: 10px;
	margin-right: 20px;
}

::-webkit-scrollbar-thumb {
	background: grey;
	border-radius: 10px;
}

::-webkit-scrollbar-track {
	background: transparent;
	margin: 10px;
}

@font-face { 
	font-family: BlackPearl;
	src: url(/JackBlackPearl/static/media/BlackPearl.5743890c.ttf);
}
.ocean { 
  height: 5%;
  width: 100%;
  position:absolute;
  bottom:0;
  left:0;
  background: #015871;
}

.sun {
	padding: 100px;
	background-color: #f1d341;
	border-radius: 100%;	
}

.island {
	height: 20px;
	width: 200px;
	position: absolute;
	bottom: 200px;
	right: 50px;
	background-color: sandybrown;
	z-index: 3;
  -webkit-animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
          animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.wave {
  background: url(/JackBlackPearl/static/media/wave.fe70ddd1.svg) repeat-x; 
  position: absolute;
  top: -198px;
  width: 6400px;
	height: 198px;
	z-index: 2;
  -webkit-animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
          animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}


.wave:nth-of-type(2) {
  top: -175px;
  -webkit-animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) -.125s infinite, swell 7s ease -1.25s infinite;
          animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) -.125s infinite, swell 7s ease -1.25s infinite;
  opacity: 1;
}

@-webkit-keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@-webkit-keyframes swell {
  0%, 100% {
    -webkit-transform: translate3d(0,-25px,0);
            transform: translate3d(0,-25px,0);
  }
  50% {
    -webkit-transform: translate3d(0,5px,0);
            transform: translate3d(0,5px,0);
  }
}

@keyframes swell {
  0%, 100% {
    -webkit-transform: translate3d(0,-25px,0);
            transform: translate3d(0,-25px,0);
  }
  50% {
    -webkit-transform: translate3d(0,5px,0);
            transform: translate3d(0,5px,0);
  }
}

.box-drift {
  position: absolute;
  width: 100px;
  height: 100%;
	-webkit-animation: doDrift 16s infinite;
	        animation: doDrift 16s infinite;
	bottom: 180px;
	z-index: 3;
}

.box-bob {
  position: absolute;
  top: 55%;   width: 100px;
  background: none;
  -webkit-animation: doBob 2s infinite;
          animation: doBob 2s infinite;
	z-index: 3;
}

.box-pitch {
  height: 100px;
  -webkit-animation: doPitch 1s infinite;
          animation: doPitch 1s infinite;
	z-index: 3;
}

.left-sail {
  background: whitesmoke;
  position: absolute;
  top: 20%;
  left: 5%;
  width: 40%;
  height: 40%;
  -webkit-clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0);
}

.right-sail {
  background: whitesmoke;
  position: absolute;
  top: 5%;
  left: 50%;
  width: 45%;
  height: 55%;
  -webkit-clip-path: polygon(0 0, 0% 100%, 100% 100%);
  clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0);
}

.hull {
  background: sandybrown;
  position: absolute;
  top: 65%;
  left: 10%;
  width: 80%;
	height: 25%;
	z-index: 3;
  -webkit-clip-path: polygon(0 0, 100% 0, 79% 100%, 18% 100%);
  clip-path: polygon(0 0, 100% 0, 79% 100%, 18% 100%);
}

@-webkit-keyframes doBob {
  0% {
    top: 55%;
  }
  50% {
    top: 47%;
  }
  100% {
    top: 55%;
  }
}

@keyframes doBob {
  0% {
    top: 55%;
  }
  50% {
    top: 47%;
  }
  100% {
    top: 55%;
  }
}

@-webkit-keyframes doDrift {
  0% {
    left: 0%;
  }
  10% {
    left: 10%;
  }
  20% {
    left: 20%;
  }
  50% {
    left: 50%;
  }
  50% {
    left: 50%;
  }
  80% {
    left: 80%;
  }
  
  100% {
    left: 90%;
  }
}

@keyframes doDrift {
  0% {
    left: 0%;
  }
  10% {
    left: 10%;
  }
  20% {
    left: 20%;
  }
  50% {
    left: 50%;
  }
  50% {
    left: 50%;
  }
  80% {
    left: 80%;
  }
  
  100% {
    left: 90%;
  }
}

@-webkit-keyframes doPitch {
  0% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
  50% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}

@keyframes doPitch {
  0% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
  50% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}

.main {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100vh;
}

.wheel {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
	height: 400px;
	position: absolute;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
	top: calc(50vh - 200px);
	z-index: -1;
}

.wheel.active {
	top: -70px;
}

.textbox {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background-color: rgba( 255, 255, 255, .7);
	border-radius: 10px;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
	padding: 40px;
	height: 170px;
	width: 600px;
	box-shadow: #0000004a 0px 15px 25px 5px;
	position: absolute;
	top: calc(50vh - 125px);
}

.textbox.active {
	top: 20px;
}

.textbox h1 {
	text-align: center;
}

.textbox .tag {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.menu {
	display: flex;
	margin-top: 30px;
}

.menuButton {
	padding: 10px 20px;
	margin: 0 10px;
	text-decoration: none;
	color: white;
	background-color: #c7886b;
	border-radius: 5px;
	position: relative;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
	cursor: pointer;
}

.menuButton:hover{
	-webkit-transform: translateY(-5px);
	        transform: translateY(-5px)
}

.wj {
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
	text-decoration: none;
	color: black;
}

.wj:hover {
	color: #006600;
}

.data {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	width: 600px;
	margin-top: 300px;
	background-color: rgba( 255, 255, 255, .7);
	border-radius: 10px;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
	padding: 40px;
	z-index: 2;
	box-shadow: #0000004a 0px 15px 25px 5px;
}

.data.active {
	-webkit-animation: load-in 1s forwards;
	        animation: load-in 1s forwards;
}


.people {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
	grid-column-gap: 50px;
	grid-row-gap: 25px;
}

.people > div {
	grid-column: span 2;
	justify-content: center;
	align-items: center;
	display: flex;
	flex-direction: column;

}

.people > div h3 {
	margin: 0;
	margin-top: 10px;
	text-align: center;
}

.people > div:first-child {
	grid-column: span 6;
}

.people > div:nth-last-child(2), .people > div:last-child {
	grid-column: span 3;
}

.history {
	padding-right: 10px;
	border-right: 1px grey solid;
	overflow-x: visible 
}

.date {
	background-color: rgba( 255, 255, 255, .6);
	border-radius: 10px;
	padding: 10px 20px;
	cursor: pointer;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

.did {
	margin-left: 10px;
	padding: 10px;
	background-color: rgba( 255, 255, 255, .6);
	border-radius: 10px;
	height: 282px;
	width: 100%;
	overflow-y: scroll;
}

.did > *:first-child {
	margin-top: 0;
}

.did > *:last-child  {
	margin-bottom: 0;
}

.did p {
	font-size: 1.2rem;
}

.did h2 {
	font-size: 2rem;
}

.table {
	border: 0px;
}

.table #doc-title {
	display: none;
}

.img { 
	height: 160px;
	width: 160px; 
	top: -140px; 
	position: absolute;
	border: 5px white solid;
	border-radius: 100%;
	background-size: 110%; 
	background-position: center;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.img.active {
	-webkit-animation: .5s fade-out forwards;
	        animation: .5s fade-out forwards
}

h1, h3, h4, h5 {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 0;
	font-family: BlackPearl;
}

h2, a, p, li {
	font-family: BlackPearl;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes load-in {
	from {
		opacity: 0;
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	to {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes load-in {
	from {
		opacity: 0;
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	to {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}


@-webkit-keyframes fade-out {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}


@keyframes fade-out {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}
